import config from '../../components/config/config.vue'
export default {
	data() {
		return {
			username:"",
			password:"",
		}
	},
	methods: {
		txtshow(mess){
			uni.showToast({
				icon:"none",
			    title: mess,
			    duration: 2000
			});
		},
		submitlogin(){
			if(this.username.length>=5&&this.username.length<=30){
				if(this.password.length>=8&&this.password.length<=40){
					uni.request({
					    url: config.httpUrl +'/api/v1/user/login',
					    data: {
					          'user_name':this.username,
							  'password':this.password,
					    },
						method:"POST",
					    header: {
					       'content-type': 'application/x-www-form-urlencoded',
						   'Authorization' : '',
					    },
					    success: (res) => {
							var jsondata=res.data
							if(jsondata.code=='0'){//登录成功
								//console.log(res)
								this.txtshow('登录成功')
								uni.removeStorageSync('token');
								//存储token
								uni.setStorageSync('token', jsondata.token)
								//启动websocket
								
								
								
								uni.switchTab({
									url: '../show/home'
								})
								
							}else{
								this.txtshow(jsondata.msg)
							}								
					    },
						fail: () =>{  
							this.txtshow('网络异常')
					     }, 
					});
				}else{
					this.txtshow('密码长度8-40')
				}
			}else{
				this.txtshow('用户名长度5-30')
			}
		},
		register(){
			uni.navigateTo({
			    url: '../user/register'
			});
		}
	},
	onLoad() {
		
	},
	onReady() {

	}
}
